import Options from './options';
import Content from './content';

export default class extends React.Component{	
	constructor(props) {
	    super(props)
	    this.state = { 
	    	itemsPerPage: 16,	    	
	    	currentpagenumber:1,
	    	actualItems: 16
	    }
	    this.handleClickPagination = this.handleClickPagination.bind(this);
	}

	handleClickPagination(arg) {
		const {currentpagenumber} = this.state		
		let newPage = arg == 'nextPage' ? currentpagenumber + 1 : currentpagenumber - 1
        this.setState({
          currentpagenumber: Number(newPage)
        });
    }

	render(){
		const { products } = this.props
		const { currentpagenumber, actualItems, itemsPerPage } = this.state		
		
		// paginado
		const totalProducts = products.length
		const indexOfLastProducts = currentpagenumber * itemsPerPage;
        const indexOfFirstProducts = indexOfLastProducts - itemsPerPage;
        const currentProducts = products.slice(indexOfFirstProducts, indexOfLastProducts);
		
	    return (
	        <div className="container">       	
	    		<Options section="header" actual={actualItems} total={totalProducts} event={this.handleClickPagination} currentpagenumber={currentpagenumber}></Options>
	        	<Content 
	        		currentProducts={currentProducts}		
	        	>
	        	</Content>
	            <Options section="footer" actual={actualItems} total={totalProducts} event={this.handleClickPagination} currentpagenumber={currentpagenumber}></Options>
	        </div>
    	);	
	}	
}


